import React, { useEffect } from "react"
import Lenis from "@studio-freight/lenis"

const ScrollEffect = () => {
  useEffect(() => {
    // 
    if (typeof window !== "undefined") {
      const lenis = new Lenis()

      const raf = time => {
        lenis.raf(time)
        requestAnimationFrame(raf)
      }

      requestAnimationFrame(raf)

      // 
      return () => {
        lenis.destroy()
      }
    }
  }, [])

  return null // UI
}

export default ScrollEffect
